import React from 'react';
import { IBasicButton, IToolbarButtonConfig, IconTranslate } from '@ali/4ever-component';
import { PluginRoles } from '@ali/4ever-bamboo';
import { Controller } from '@ali/4ever-cangjie';
;

export interface ItranslateButtonProps extends IToolbarButtonConfig {
  locale?: Record<string, string>;
  config?: any;
  controller: Controller;
}

export default function createStickerButton(props: ItranslateButtonProps): IBasicButton {
  const { controller, locale, icon, title, tooltip, badgeDot, disabled, config } = props;

  const { triggerTranslateSideMenu = () => { } } = config;
  const handleClick = () => {
    triggerTranslateSideMenu();
  };

  const isDisabled = disabled || controller.query('isSelectionInListSymbol') || false;

  const buttonConfig: IBasicButton = ({
    icon: icon || <IconTranslate />,
    onClick: handleClick,
    title: title || locale?.title,
    tooltip: {
      title: title || locale?.title || '',
      ...tooltip,
    },
    badgeDot,
    testid: "translate_button",
    role: PluginRoles.translate,
    disabled: isDisabled,
  });

  return buttonConfig;

}

